<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">{{$route.query.type == 1 ? '班级荣誉' :'班级风采'}}</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="manager">管理</span>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index" @click="$router.push({path:'/honorDetail',query:{type:$route.query.type,id:item.id}})">
                    <img :src="$api.fileUrl + item.cover_image">
                    <span>{{item.title}}</span>
                </li>
            </ul>
        </div>
        <div class="centerBtns f26 cfff text-center" @click="next">新增文章</div>
        <van-popup v-model="show" position="right" class="vantPop">
            <div>  
                <div class="loginNav">
                    <img src="../../../assets/login/return.jpg" alt="" class="return" @click="closePop">
                    <p class="title f30 cfff text-center">班级荣誉编辑</p>
                    <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="closePop">取消</span>
                </div>
                <div class="content f28 c333" style="padding:0.84rem 0 1rem;">
                    <ul class="pl-30">
                        <li class="b_line pr-30" v-for="(item,index) in list" :key="index" @click="isChecked(index)">
                            <img style="border:none;" :src="item.checked?checked:notCheck"/>
                            <img class="headPic" :src="$api.fileUrl + item.cover_image"/>
                            <span>{{item.title}}</span>
                        </li>
                    </ul>
                </div>
                <div class="centerBtn f26 c333 text-center pl-30">
                    <div>
                        <img class="pr-20" :src="checkedAll?checked:notCheck" alt="" @click="all">
                        全选
                    </div>
                    <span class="cfff" @click="delBtn">删除</span>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: '',
            list:[],
            show:false,
            notCheck:require('../../../assets/message/notCheck.png'),
            checked:require('../../../assets/message/checked.png'),
            checkedAll:false,
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/class'});//返回上一层
        },
        closePop(){
            this.show = false;
        },
        next(){
            this.$router.push({path:'/editHonor',query:{type:this.$route.query.type}})
        },
        isChecked(index){
            this.list[index].checked=!this.list[index].checked;
        },
        all(){
            this.checkedAll=!this.checkedAll;
            var list=this.list;
            for(var i in list){
                list[i].checked=this.checkedAll;
            }
            this.list=list;
        },
        delBtn(){
            
            this.$toast.loading({
                mask: true,
                duration: 0
            });
            var arr = [];
            for(var i in this.list){
                if(this.list[i].checked){
                    arr.push(this.list[i].id)
                }
            }
            if(arr.length == 0){
                this.$toast('至少选择一项');
                return
            }  
            if(this.$route.query.type == 1){
                var param = {
                    id : arr.toString(),
                    isdel : 2
                }
                this.$http_json(this.$api.class.insertOrUpdateInfoHonour,param).then(data => {
                    console.log(data);
                    if(data.code == 0){
                        for(var j in arr){
                            this.list.splice(j,1);
                        }
                        this.$toast.clear();
                    }else{
                        this.$toast(data.msg);
                    }
                    
                },err => {
                    this.$toast.clear();
                })
            }else{
                var param = {
                    id : arr.toString(),
                    isdel : 2
                }
                this.$http_json(this.$api.class.insertOrUpdateInfoMien,param).then(data => {
                    console.log(data);
                    if(data.code == 0){
                        for(var j in arr){
                            this.list.splice(j,1);
                        }
                        this.$toast.clear();
                    }else{
                        this.$toast(data.msg);
                    }
                },err => {
                    this.$toast.clear();
                })
            }
        },
        save(){

        },
        manager(){
            this.show = true;
        },
        getHonorList(){//获取列表
            this.$http({url:this.$api.class.getHonorList},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        data.data[i].checked = false;
                    }
                    this.list = data.data;
                }
            })
        },
        getHonorList(){//获取荣誉列表
            this.$http({url:this.$api.class.getHonorList},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        data.data[i].checked = false;
                    }
                    this.list = data.data;
                }
            })
        },
        getMienList(){////获取风采列表
            this.$http({url:this.$api.class.getMienList},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }
            })
        },
    },
    created() {
        var type = this.$route.query.type;
        if(type == 1){//1荣誉2风采
            this.getHonorList();
        }else{
            this.getMienList();
        }
    },
}
</script>

<style scoped>
    .vantPop{position: fixed;top: 50%;right: 0;height: 100vh;left: 0;}
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    
    .content{
        line-height: 1.94rem;
    }
    .content img{
        width:2.4rem;
        height:1.34rem;
        margin-right: .3rem;
        border-radius: .1rem;
        border:1px solid #EBEBEB;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
    .centerBtns{height: 0.9rem;width: 5rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff; position: fixed;bottom: 0.63rem;left: 50%;margin-left: -2.5rem; }

     /* 修改组件样式 */
    .vantPop .loginNav{
        position: fixed;
        z-index: 3001;
    }
    .vantPop .centerBtn{
        height: 0.9rem;
        background: #F3F3F3;
        line-height: 0.9rem;
        position: fixed;
        bottom: 0;
        right:0;
        left:0;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .vantPop .centerBtn span{
        width:2rem;
        background: #E74444;
        text-align: center;
        display: block;
    }
    .vantPop .centerBtn img{
        width:.4rem;
        height:.4rem;
    }
    .vantPop .content{
        line-height: 1.94rem ;
    }
    .vantPop li img:first-child{
        width:.4rem;
        height:.4rem;
        margin-right: .3rem;
    }
    .vantPop .headPic{
        width:2.4rem;
        height:1.34rem;
        border-radius: .1rem;
        margin-right: .3rem;
        border:1px solid #EBEBEB;
    }
    .vantPop li{
        display: flex;
        align-items: center;
        /* padding: 0.3rem 0; */
    }
    .vantPop .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
    .vantPop .content div{
        position: relative;
        padding:.16rem .3rem 0;
        line-height: .34rem;
    }
    .vantPop .content div img{
        width:.24rem;
        height:.24rem;
        position: absolute;
        left:3rem;
        top:.31rem;
    }
    .vantPop .content input{
        width:100%;
        height:.54rem;
        background: #F3F3F3;
        border:0;
        border-radius: .1rem;
        text-align: center;
    }
</style>




